import React from 'react'
import './style.scss'
import PropType from 'prop-types'
import cx from 'classnames'
import anime from 'animejs'

export default class AnimatePics extends React.PureComponent {
  static defaultProps = {
    count: 10,
    activeCount: 5,
    interval: 400,
    onAnimateEnd: f => f
  }

  constructor (props) {
    super(props)
    this.state = {
      curActiveCount: 0
    }

    this.animateObj = {
      curActiveCount: 0
    }
  }

  componentDidMount () {
    this.animateChangeCount(this.props.activeCount)
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (prevProps.activeCount !== this.props.activeCount) {
      this.animateChangeCount()
    }
  }

  animateChangeCount = () => {
    let {activeCount, interval} = this.props
    anime.remove(this.animateObj)
    if (interval < 1) {
      this.setState({curActiveCount: activeCount})
      return
    }
    this.animateObj = {curActiveCount: this.state.curActiveCount}
    anime({
      targets: this.animateObj,
      curActiveCount: activeCount,
      round: 1,
      easing: 'linear',
      duration: Math.abs(activeCount - this.state.curActiveCount) * interval,
      update: () => {
        if (this.state.curActiveCount === this.animateObj.curActiveCount) return
        this.setState({curActiveCount: this.animateObj.curActiveCount})
      },
      complete: () => {
        this.props.onAnimateEnd(activeCount)
        if (this.state.curActiveCount === activeCount) return
        this.setState({curActiveCount: activeCount})
      }
    })
  }

  componentWillUnmount () {
    anime.remove(this.animateObj)
  }

  render () {
    const {props, state} = this

    return (
      <div className={cx('dui-animate-pics', props.className)} style={props.style}>
        {
          Array.from({length: props.count}).map((v, i) => {
            let itemClassName = cx('dui-animate-pics-item', props.itemClassName, {
              'active': i < state.curActiveCount
            })

            return <img src={props.src} key={i} style={props.itemStyle} className={itemClassName} />
          })
        }
      </div>
    )
  }
}

AnimatePics.propTypes = {
  src: PropType.string.isRequired
}
